.tableItem {
    position: relative;
    display: flex;
    border-bottom: solid 1px grey;
    justify-content: space-between;
    padding: 10px;
    flex-wrap: wrap;
}

.tableItem:after {
    content: '+';
    position: absolute;
    right: 8px;
    top: 10px;

    text-align: center;
    font-weight: bold;
    width: 5%;
}

.tableItem.altAfter:after {
    content: '-';
    font-size: 25px;
    top: 3px;
}

.tableItem:nth-child(odd) {
    background: lightgrey;
}
.tableItem span {
    display: block;
}

.tableItem span:first-child {
    flex: 0 1 65%;
}

.tableItem span:last-child {
    flex: 0 1 30%;
}

.tableItem .time {
    text-align: right;
    margin-right: 20px;
}

.tableItem .more {
    display: block;
    height: inherit;
    flex: 0 1 5%;
}

.tableItem .more svg {
    vertical-align: bottom;
    padding-top: 15px;
    transition: all 0.3s;
}

.tableItem .dropdownSpecs {
    display: none;
    flex: 0 1 100%;
    padding: 10px 35px;
    transition: all 0.3s;
    background: #efefef;
}

.dropdownSpecs.showList {
    display: block;
    /*transform: matrix(1, 0, 0, 1, 0, 0);*/
}

.tableItem .dropdownSpecs .dropdownItem {
    justify-content: space-between;
    margin-bottom: 5px;
}

.tableItem .dropdownSpecs .dropdownItem .dropdown__span:last-child {
    text-align: right;
}

.tableItem .dropdownSpecs .dropdownItem .dropdown__span {
    flex: 0 1 auto;
}