.header {
    text-align: center;
    background: #000;
    color: #fff;
    font-size: 10px;
}

.table {
    width: 90%;
    max-width: 320px;
    margin: 10px auto 80px;
}

.tableHeader {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    margin-bottom: 10px;
    border-bottom: solid 1px grey;
}

.tableHeader .table__title {
    flex: 0 1 100%;
    text-align: center;
    margin-bottom: 10px;
}

.tableHeader span {
    display: block;
    text-transform: capitalize;
    font-weight: bold;
    font-size: 18px;
}

.tableHeader [class*='timeHeader'] {
    margin-right: 30px;
    text-decoration: underline;
}

.timeRenderer {
    display: none;
}